// stylelint-disable at-rule-empty-line-before
@import "../../node_modules/normalize-css/normalize";

@import "variables";

@font-face {
	font-family: 'CoFo Sans';
	src: url('../fonts/CoFoSans-Bold.eot');
	src: local('CoFo Sans Bold'), local('CoFoSans-Bold'),
					url('../fonts/CoFoSans-Bold.eot?#iefix') format('embedded-opentype'),
					url('../fonts/CoFoSans-Bold.woff2') format('woff2'),
					url('../fonts/CoFoSans-Bold.woff') format('woff'),
					url('../fonts/CoFoSans-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}
@font-face {
	font-family: 'CoFo Sans';
	src: url('../fonts/CoFoSans-Regular.eot');
	src: local('CoFo Sans'), local('CoFoSans-Regular'),
					url('../fonts/CoFoSans-Regular.eot?#iefix') format('embedded-opentype'),
					url('../fonts/CoFoSans-Regular.woff2') format('woff2'),
					url('../fonts/CoFoSans-Regular.woff') format('woff'),
					url('../fonts/CoFoSans-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
$font-family-montserrat: "Montserrat", sans-serif;
$font-family-CoFoSans: "CoFo Sans", sans-serif;

*,
*::before,
*::after {
	box-sizing: border-box;
}
ul,
ol {list-style: none;margin: 0;padding: 0;}
ul ul,
ol ul,
ul ol,
ol ol {
		list-style: none;
		margin: 0;padding: 0;
}
a, li, button, input, textarea {
				outline: none !important;
}
.clear {
				clear: both;
}

html, body {
	box-sizing: border-box;
}

body {
	background: $color-white;
}
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}


.container {
	margin: 0 auto;
	display: block;
	width: 1160px;
}
.not__found-wrapper {
	@extend .d-block; 
	overflow: hidden;
	padding-bottom: 40px;
	position: relative;

	.container {
		position: relative;
		z-index: 5;
	}
}
.not__found-bg {
	position: absolute;
	left: 0;
	bottom: 0;
	background: url($img_dir+"icons/bg-404.svg") no-repeat center bottom;
	display: block;
	width: 100%;
	height: 600px;
	z-index: 1;
}
.not__found-header {
	@extend .d-block; 
	margin-top: 69px;
}
.not__found-header-container {
	display: block;
	width: 100%;
	max-width: 602px;
	margin: 0 auto;
}
.not__found-header-content {
	display: flex;
	width: 100%;
	justify-content: space-between;
}
.not__found-header-container-logo-edc {
	width: 220px;
	margin-top: 13px;
	margin-left: 12px;
}
.not__found-header-container-logo-misis {
	width: 220px;
}

.not__found-images {
	@extend .d-block; 
	margin-top: 75px;
}
.not__found-images-container {
	display: block;
	width: 100%;
	max-width: 621px;
	margin: 0 auto; 
	position: relative;
	padding: 130px 0 7px;
}
.images-text {
	position: relative;
	z-index: 3;
}

.not__found-img-kite {
	width: 170px;
	position: absolute;
	left: 118px;
	top: 28px;
	z-index: 2;
}
.not__found-img-play {
	width: 80px;
	position: absolute;
	top: 74px;
	left: 348px;
	z-index: 4; 
}
.not__found-img-user-one {
	width: 140px;
	position: absolute;
	top: 15px;
	right: -33px;
	z-index: 4; 
}
.not__found-img-user-two {
	width: 170px;
	position: absolute;
	bottom: 0;
	left: 20px;
	z-index: 4; 
}
.not__found-img-user-three {
	width: 100px;
	position: absolute;
	bottom: 0;
	left: 355px;
	z-index: 4; 
}
.not__found-img-user-book {
	width: 80px;
	position: absolute;
	bottom: 1px;
	right: -24px;
	z-index: 4; 
}
.not__found-form {
	@extend .d-block; 
	margin-top: 75px;
}
.not__found-title {
	@extend .d-block; 
	margin: 0;
	padding: 0;
	text-align: center;
	color: $color-red3;
	font: 24px/29px $font-family-montserrat;
	font-weight: 900;
	text-transform: uppercase;
	margin-bottom: 22px;
}
.not__found-sub-title {
	@extend .d-block; 
	margin: 0;
	padding: 0;
	text-align: center;
	color: $color-grey5;
	font: 24px/29px $font-family-montserrat;
	font-weight: 600; 
	margin-bottom: 31px;
}
.not__found-form-container {
	@extend .d-block; 
	background: $color-white;
	position: relative;
	z-index: 5;
}
.not__found-form-row {
	display: flex;
	margin: 0 -20px;
}
.not__found-form-col {
	padding: 0 20px;
}
.not__found-form-col {
	width: 33.33333333%;

	@media (min-width: 960px) {
		width: 47%;

		&.form-col-26 {
			width: 26%;
		} 
	}

}
.not__found-form-col-action {

	@media (min-width: 960px) {
		width: 27%;
	}
}
.not__found-form-input {
	@extend .d-block; 
	font: 24px/24px $font-family-montserrat;
	font-weight: 500;  
	background: $color-white;
	border: 2px solid $color-blue6;
	padding: 0 20px;
	height: 62px;
	color: $color-grey;
	transition: color 0.3s;

	&:focus {
		color: rgba($color-black, .5);
	}
}
.not__found-form-btn {
	@extend .d-block; 
	height: 62px;
	background: $color-red;
	font: 18px/62px $font-family-montserrat;
	font-weight: 500;
	border: 0;
	padding: 0;
	transition: background 0.3s;
	text-transform: uppercase;
	letter-spacing: 1px;
	text-align: center;
	color: $color-white;

	&:hover {
		background: $color-blue;
	}
}
.not__found-article {
	@extend .d-block;
	margin-top: 84px;
}
.not__found-article-title {
	@extend .d-block;
	text-align: center;
	color: $color-grey5;
	font: 22px/29px $font-family-montserrat;
	font-weight: 600;
	margin-bottom: 27px;
}
.not__found-article-content {
	@extend .d-block;
}
.not__found-article-row {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -20px;
}
.not__found-article-col {
	padding: 0 20px;
	width: 33.33333333%;
	margin: 20px 0;
}
.not__found-article-box {
	@extend .d-block;
	position: relative;

	&::before {
		@extend .before;
		background: $color-blue;
		left: 0;
		top: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		transform: translateX(4px) translateY(4px);  
		transition: transform 0.2s;
	}

	&:hover {
		&::before { transform: translateX(0) translateY(0); }
	}
}
.not__found-article-link {
	@extend .d-block;
	position: relative;
	z-index: 2;
	text-decoration: none;
}
.nf-article-img {
	@extend .d-block;

	img {
		float: left;
		width: 100%;
	}
}
.nf-article-content {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	display: block;
	z-index: 3;
	padding: 22px 20px 15px;
}
.nf-article-title {
	@extend .d-block; 
	color: $color-white;
	font: 24px/29px $font-family-montserrat;
	font-weight: 800;
	letter-spacing: 1px;
	text-transform: uppercase;
	margin-bottom: 14px;
}
.nf-article-text {
	@extend .d-block; 
	font: 18px/24px $font-family-montserrat;
	color: $color-white;
	font-weight: 600;

	span {
		text-decoration-line: underline;
	}

}
.nf-article-style2 {
	.not__found-article-link {
		background: $color-grey6;
	}

	.not__found-article-box, .not__found-article-link {
		height: 100%;
	}
}
.nf-article-grey {
	display: flex;
	flex-direction: column; 
	padding: 0 20px 25px;
	width: 100%;
}
.nf-article-logo {
	height: 95px;
	display: flex;
	align-items: center;
}
.nf-logo-1 {
	img {
		width: 117px;
	}
}
.nf-logo-2 {
	img {
		width: 112px;
	}
}
.nf-logo-3 {
	img {
		width: 83px;
	}
}
.nf-article-title2 {
	@extend .d-block; 
	font-family: Montserrat;
	font: 16px/22px $font-family-montserrat;
	font-weight: 600;
	color: $color-grey5;
}
footer {
	@extend .d-block; 
	background: $color-violet2;
	overflow: hidden;
	padding: 50px 0 110px;
}
.footer-top-container {
	@extend .d-block; 
}
.footer-row {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -20px;
}
.footer-col {
	padding: 0 20px;
	width: 33.33333333%;	
}
.footer-logo {
	width: 162px;
}
.footer-title {
	@extend .d-block;
	font: 18px/20px $font-family-CoFoSans;
	color: $color-white;
	font-weight: bold;
	margin-bottom: 12px;
}
.footer-list {
	@extend .d-block;

	li {
		@extend .d-block;
		margin-bottom: 4px;

		a {
			display: inline-block;
			float: left;
			font: 18px/22px $font-family-CoFoSans;
			color: $color-white;
			font-weight: normal;
			text-decoration: none;
			white-space: nowrap;

			&.f-email {
				border-bottom: 2px solid $color-blue;
			}
		}

		div {
			display: flex;
			align-items: center;
		}
	}
}
.f-whatsapp {
	display: inline-block;
	float: left;
	margin-left: 8px;
	margin-top: 1px;
}
.icon-whatsapp {
	width: 15px;
	height: 15px;
}
.footer-social {
	display: flex;
	width: 100%;
	float: left;
	margin-top: 7px;

	li {
		width: 46px;
		margin: 0 11px;

		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}

		a {
			text-decoration: none !important;
		}
	}
}
.footer-bottom-container {
	@extend .d-block;
	margin-top: 30px;
}
.footer-copy {
	@extend .d-block;
	font: 18px/23px $font-family-CoFoSans;
	color: $color-white;
	font-weight: normal;	
}
.not-found-section {
	@extend .d-block;
}
.xs-visible {
	display: none !important;
}

// .is-device-mobile, .is-device-tablet
.is-device-mobile, .is-device-tablet {
	
	.not__found-form-btn {
		&:hover {
			background: $color-red;
		}
	}

	.not__found-article-box {
		&:hover::before {
			transform: translateX(4px) translateY(4px);  
		}
	}	

}// end .is-device-mobile, .is-device-tablet


$feature: max-width;
$mediaWidth1: 1365px;
$mediaWidth2: 1199px;
$mediaWidth3: 1023px;
$mediaWidth4: 959px;
$mediaWidth5: 767px;
$mediaWidth6: 479px;


/* START #{$mediaWidth1} */ //-1365px
@media ($feature: $mediaWidth1) {
	
}/* END #{$mediaWidth1} */ //-1365px

/* START #{$mediaWidth2} */ //-1199px
@media ($feature: $mediaWidth2) {
	.container {
		width: 90%;
	}
	.nf-article-title {
		font-size: 20px;
		line-height: 26px;
		margin-bottom: 10px;
	}
	.nf-article-content {
		padding: 17px 15px 15px;
	}
	.nf-article-img {
		min-height: 120px;
		overflow: hidden;

		img {
			height: 100%;
			max-width: inherit;
			width: auto;
		}
	}
	.nf-article-text {
		font-size: 16px;
		line-height: 22px;
	}
	.nf-article-grey {
		padding: 0 15px 20px;
	}
}/* END #{$mediaWidth2} */ //-1199px

/* START #{$mediaWidth3} */ //-1023px
@media ($feature: $mediaWidth3) {
	.container {
		width: 100%;
		padding: 0 20px;
	}
}/* END #{$mediaWidth3} */ //-1023px

/* START #{$mediaWidth4} */ //-959px
@media ($feature: $mediaWidth4) {
	.not__found-header-container-logo-edc,
	.not__found-header-container-logo-misis {
		width: 160px;
	}
	.not__found-header-container-logo-edc {
		margin-left: 0;
		margin-top: 7px;
	}
	.not__found-header {
		margin-top: 35px;
	}
	.not__found-form-col {
		padding: 0 10px;
	}
	.not__found-form-row {
		margin: 0 -10px;
	}
	.not__found-form-input {
		height: 45px;
		padding: 0 10px;
		font-size: 16px;
		line-height: 16px;
	}
	.not__found-form-btn {
		height: 45px;
		font-size: 14px;
		line-height: 45px;		
	}
	.not__found-title {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 12px;
	}
	.not__found-sub-title {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 20px;
	}
	.not__found-images {
		margin-top: 50px;
	}
	.not__found-form {
		margin-top: 40px;
	}
	.not__found-article {
		margin-top: 50px;
	}
	.not__found-article-row {
		width: 50%;
		margin: 0;
		float: left;
		padding-right: 20px;

		&.nf-article-style2 {
			padding-right: 0;
			padding-left: 20px;
		}
	}
	.not__found-article-col {
		width: 100%;
	}
	.not__found-form-container {
		width: 728px;
		float: none;
		margin: 0 auto;
	}
	.not__found-article-content {
		width: 728px;
		float: none;
		margin: 0 auto;		
		display: flex;
		justify-content: space-between;
	}
	.not__found-article-col {
		padding: 0;
	}
	.nf-logo-1 img,
	.nf-logo-2 img {
		width: 90px;
	}
	.nf-logo-3 img {
		width: 60px;
	}
	.nf-article-grey {
		flex-direction: row;
		padding: 17px 15px 20px;
		height: 120px;
	}
	.nf-article-logo {
		height: auto;
		width: 170px;
	}
	.nf-article-style2 .not__found-article-box, 
	.nf-article-style2 .not__found-article-link {
		height: auto;
	}
	.nf-article-img {
		height: 120px;

		img {
			height: auto;
			width: 100%;
		}
	}
	.nf-article-title2 {
		font-size: 14px;
		line-height: 20px;
	}
	footer {
		padding: 30px 0 40px;

		.container {
			width: 768px;
			float: none;
			margin: 0 auto;
		}
	}
	.footer-logo {
		width: 130px;
	}
	.footer-title {
		font-size: 16px;
		line-height: 18px;
	}
	.footer-list li a {
		font-size: 16px;
		line-height: 18px;		
	}
	.footer-copy {
		font-size: 16px;
		line-height: 18px;			
	}
	.footer-social li {
		width: 30px;
		margin: 0 8px;
	}
	.footer-bottom-container {
		margin-top: 20px;
	}
	.nf-article-title2 {
		display: flex;
		align-items: center;
	}
}/* END #{$mediaWidth4} */ //-959px


/* START #{$mediaWidth5} */ //-767px
@media ($feature: $mediaWidth5) {
	.container {
		padding: 0 10px;
	}
	.not__found-header-container-logo-edc, .not__found-header-container-logo-misis {
		width: 140px;
	}
	.not__found-header {
		margin-top: 20px;
	}
	.not__found-images-container {
		width: 460px;
		padding: 90px 0 7px;
	}
	.not__found-img-user-two {
		width: 100px;
		left: 10px;
	}
	.not__found-img-kite {
		width: 110px;
		left: 92px;
		top: 22px;
	}
	.not__found-img-user-three {
		left: 260px;
		width: 60px;
	}
	.not__found-img-user-one {
		top: 4px;
		right: 11px;
		width: 70px;
	}
	.not__found-img-play {
		width: 40px;
		top: 56px;
		left: 260px;		
	}
	.not__found-img-user-book {
		width: 50px;
		right: -10px;
	}
	.not__found-images {
		margin-top: 30px;
	}
	.not__found-form-row {
		margin: 0;
		width: 100%;
		display: block;
	}
	.not__found-form-col {
		display: block;
		float: left;
		padding: 0;
		width: 100%;
		margin: 10px 0;
	}
	.not__found-form-container {
		width: 100%;
		display: block;
		float: left;
	}
	.not__found-article-row {
		padding: 0;

		&.nf-article-style2 {
			padding: 0;
		}
	}
	.not__found-article-row {
		width: 100%;
	}
	.not__found-article-content {
		width: 100%;
		display: block;
	}
	.nf-article-img {
		height: auto;
		min-height: 1px;
	}
	.nf-article-img img {
		width: auto;
		max-width: 100%;
		height: auto;
	}
	.not__found-article-box {
		width: 100%;
		max-width: 360px;
	}
	.not__found-article-col {
		display: flex;
		justify-content: center;
		margin: 15px 0;
	}
	.not__found-bg {
		height: 300px;
		background-size: cover;
	}
	.footer-logo {
		width: 110px;
	}
	.footer-row {
		margin: 0 -10px;
	}
	.footer-col {
		padding: 0 10px;
		width: 50%;
	}
	.footer-top-container {
		.footer-row {
			justify-content: flex-end;
		}
	}
	.footer-title {
		text-align: right;
	}
	.footer-list li a {
		float: right;
	}
	.footer-list li div {
		justify-content: flex-end;
	}
	.footer-social {
		justify-content: flex-end;
	}
	.footer-list {
		margin-bottom: 15px;
	}
	.xs-hidden {
		display: none !important;
	}
	.xs-visible {
		display: block !important;
	}
	.footer-copy {
		margin-top: 10px;
	}
	footer .container {
		width: 100%;
		float: left;
	}

}/* END #{$mediaWidth5} */ //-767px


/* START #{$mediaWidth6} */ //-479px
@media ($feature: $mediaWidth6) {
	.not__found-header-container-logo-edc, .not__found-header-container-logo-misis {
		width: 90px;
	}
	.not__found-article-title {
		font-size: 18px;
		line-height: 26px;
	}
	.not__found-title, .not__found-sub-title {
		font-size: 18px;
		line-height: 22px;		
	}
	.footer-title {
		font-size: 14px;
		line-height: 16px;		
	}
	.footer-list li a {
	 font-size: 14px;
	 line-height: 16px;
	}	
	.footer-copy {
	 font-size: 14px;
	}
	.footer-social li {
		margin: 0 4px;
	}
	footer {
		padding: 30px 0;
	}
	.not__found-images-container {
		width: 300px;
		padding: 50px 0 7px;
	}
	.not__found-img-user-two {
		width: 60px;
		left: 5px;
	}
	.not__found-img-kite {
		width: 70px;
		left: 60px;
		top: 7px;
	}
	.not__found-img-user-three {
		left: 165px;
		width: 40px;
	}
	.not__found-img-user-one {
		top: -13px;
		right: 6px;
		width: 50px;
	}
	.not__found-img-play {
		width: 25px;
		top: 28px;
		left: 165px;		
	}
	.not__found-img-user-book {
		width: 30px;
		right: 0;
	}	

}/* END #{$mediaWidth6} */ //-479px


